.home-section {
  background: #361999;
  background: linear-gradient(
    -45deg,
    rgba(54, 25, 153, 1) 0%,
    rgba(71, 23, 233, 1) 100%
  );
  width: 100%;
  height: 100vh;
}

.home-container {
  width: 65rem;
  margin: auto;
  padding-top: 12rem;
}

.home-heading {
  text-align: left;
}

.home-headingDesc {
  font-family: Raleway;
  color: #fff;
  font-weight: 400;
  font-size: 3rem;
}

.home-socialMedia{
  width: 7.5rem;
  display: flex;
  margin-top: 2rem;
  flex-direction: row;
  justify-content: space-between;
}

.home-socialMedia a{
  transition: all .15s ease-in-out;
}

.home-socialMedia a:hover{
  transform: translate(-3px,-10px);
}