.contactSection{
    width: 100%;
    height: 100vh;
    background-color: #fff;    
    font-family: Raleway;
}

.contact-container{
    width: 51.938rem;
    margin: auto;
    padding-top: 13.3rem;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.contact-heading{
    color: #616161;
    font-family: Raleway;
    font-weight: 700;
    font-size: 3rem;
    text-align: left;
}

.contact-container form{
    width: 21.875rem;
    height: 24rem;
    margin-top: 3.125rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.contact-container form div{
    display: flex;
    flex-direction: column;
}

.contact-container form label{
    font-family: Raleway;
    font-size: 0.875rem;
    color: #616161;
}

.contact-container form input{
    border-radius: 5px;
    border: 1px solid #F5F5F5;
    outline:none;
    height: 1.875rem;
    font-family: Raleway;
    padding: 0.25rem;
}

.contact-container form textarea{
    height: 8.75rem;
    resize:none;
    padding: 0.25rem;
    border-radius: 5px;
    border: 1px solid #F5F5F5;
    outline:none;
    font-family: Raleway;
}

.contact-container form button{
    width: 6.25rem;
    height: 1.875rem;
    border-radius: 1.25rem;
    border: none;
    outline:none;
    background-color: #3491FF;
    color: #fff;
}

.contact-description{
    height: 22rem;
    width: 26.563rem;
    margin: 7.5rem 3.5rem;
    display: flex;
    flex-direction: column;
}

.contact-descPersonal{
    height: 18.25rem;
}

.contact-PersonalContainer{
    margin-top: 3.125rem;
}

.contactPersonal{
    display:flex;
    flex-direction: row;
    line-height: 40px;
}

.contactPersonal a{
    margin-left: 1.25rem;
    text-decoration: none;
    color: #000;
}

.contactPersonal a:hover{
    text-decoration: underline;
}

.contact-socialMediaContainer{
    width: 4.625rem;
    display: flex;
    justify-content: space-between;
}

