.navbar-nav {
  padding: 2rem;
  position: fixed;
}

.navbar-ul {
  height: 10.75rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.navbar-li{
    height: 2.15rem;
    list-style: none;
}

.link{
    font-family: Raleway;
    text-decoration: none;
    color: #FFFFFF60;
    line-height: 2.15rem;
    transition: all .05s ease-in;
}

.link:hover{
    font-size: 1.5rem;
    color: #FFFFFF;
}

.navbar-active{
    font-weight: 600;
    color: #FFFFFF;
}

.navbar-active .link{
    font-size: 1.5rem;
}

.nav-black{
    color: #000;
}

.nav-black:hover{
    font-size: 1.5rem;
    color: #000000;
}


.nav-davidsteenmann .link{
    color: #78FFF1; 
    font-weight: 600;
}

.nav-about .link{
    color: #4BBE6B;
    font-weight: 600;
}

.nav-skills .link{
    color: #F0DB4F;
    font-weight: 600;
}

.nav-projects .link{
    color: #EF6351;
    font-weight: 600;
}

.nav-contact .link{
    color: #3491FF;
    font-weight: 600;
}