.skillsSection{
    width: 100%;
    height: 100vh;
    background-color: #202020;
}

.skillsHeading{
    padding-top: 10rem;
}

.skills-animatedContainer{
    margin-top: 1rem;
}

.skills-animatedContainer h2{
    margin-bottom: 1rem;
    text-align: center;
    font-size: 2rem;
    color: #fff;
    width: 100%;
}

.skills-container{
    width: 36.5rem;
    height: 15.625rem;
    padding: 1rem;
    margin: auto;
    background-color: #FFFFFF05;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.skills-container div{
    margin-right: 3rem;
}

.skills-frameworkContainer{
    width: 15rem;
    display: flex;
    flex-direction:column;
    justify-content: center;
    margin: 2rem auto;
}


.skills-frameworkContainer h3{
    font-family: Raleway;
    font-size: 1.125rem;
    color: #fff;
    text-align: center;
}

.skills-frameworkContainer ul{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 1rem;
}


.skills-frameworkContainer ul li{
    list-style: none;
    font-family: Raleway;
    font-size:0.875rem;
    color: #fff;
}
