.about-section{
    height: 100vh;
    background-color:#065A60;
}

.about-container{
    width: 59.25rem;
    margin: auto;
    padding-top: 12.5rem;
}

.about-heading{
    text-align: left;
}

.about-description{
    font-size: 1.5rem;
    font-family: Raleway;
    color: #fff;    
    margin: 1rem 0 ;
}

.about-link{
    font-size: 1.125rem;
    font-family: Raleway;
    text-decoration: none;
    color: #ffffff60;
    transition: all .15s ease-in;
}

.about-link:hover{
    color: #4BBE6B;
}

