.CV{
    width:26.875rem;
    position: fixed;
    bottom: 0;
    right: 0;
    margin-right: 6rem;
    display: flex;
    flex-direction: column;
}

.CV-container{
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    width: 16rem;
}


.wjgContainer{
    margin-left: 14.75rem;
}

.CV-container div p{
    width: 13.625rem;
    color: #FFFFFF;
    font-family: Raleway;
    font-weight: 500;
    font-size: 1rem;
    margin-top: 2rem;
    line-height: 1.3rem;
}

.CV-container div p span{
    font-size: 0.8rem;
    font-weight: 300;
}

.CV-Rectangle{
    width: 1.25rem;
    height: 12.5rem;
}

.CV-fontys{
    background-color: #188737;
}

.CV-wjg{
    background-color: #4BBE6B;
}