*{
    margin: 0;
    padding: 0;
}

body{
    background-color: #f5f5f5;
    overflow-x: hidden;
}

.projectContainer{
    width: 70%;
    display: flex;
    flex-direction: row;
    margin-top: 22vh;
    color: #FFF;
    font-family: Raleway;
}

.projectContentContainer{
    width: 70%;
    height: 90%;
    margin-top: 1.5%;
    display: flex;
    flex-direction: column;
    text-align: left;
    background-color: white;
}

.projectHeader{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 2%;
    background-color: #EF6351;
}

.projectHeading{
    width: 30%;
    margin-top: 1.5%;
    font-weight: 500;
}

.projectHeading a{
    text-decoration: none;
    color: #fff;
}

.projectHeading a:hover{
    text-decoration: underline;
}

.projectTagContainer{
    width: 45%;
    display: flex;
    flex-direction:row;
}

.projectTag{
    margin: 0 15% 0 0;
    padding: 4%;
    background-color: #00000025;
    font-size: 12px;
    border-radius: 25px;
}


.projectDescription{    
    padding: 2%;
    font-size: 16px;
    color: #000;
}

.projectImg{
    width: 686px;
    height: 340px;
}